require('./assets/styles/index.less');
const faceapi = require('./assets/face-api/face-api.js');
const errorMap = {
  'NotAllowedError': '',
  'AbortError': '',
  'NotFoundError': '',
  'NotReadableError': '',
  'OverConstrainedError': '',
  'SecurityError': '',
  'TypeError': ''
};

let currentStep = '0';
const stepMap = {
  '0': {
    text: ''
  },
  '1': {
    text: ''
  },
  '2': {
    text: ''
  },
  '3': {
    text: ''
  },
  '99': {
    text: ''
  }
}
const tipsMap = {
  'long': '',
  'short': '',
  'center': ''
}

class FaceDetection {
  constructor(options) {
    this.options = Object.assign({
      matchedScore: 0.9,
      mediaSize: {
        width: 300,
        height: 300
      },
      closeEyesScore: 1.5,  // /
      openMouthScore: 1.06,  // (y)
      point31Pos: {x:0, y:0, count:0, score: 0.1} // 31
    }, options);

    this.timer = null;
    this.mediaStreamTrack = null; // 

    this.videoEl = document.querySelector('#videoEl'); // 
    this.trackBoxEl = document.querySelector('#trackBox'); // 
    this.canvasImgEl = document.querySelector('#canvasImg'); // 
    this.operationEl = document.querySelector('.operation'); // 
    // this.retryBtnEl = document.querySelector('.js-retry');
    this.compareBtnEl = document.querySelector('.js-compare');
    this.pauseBtnEl = document.querySelector('.js-pause');
    this.timeOut = 50;
    this.tipText = document.querySelector('.tip-text');

    // canvas
    this.canvas_face = document.createElement("canvas");
    this.canvas_face.width = this.options.mediaSize.width;
    this.canvas_face.height = this.options.mediaSize.height;
    this.ctx_face = this.canvas_face.getContext('2d');
    // canvas
    this.canvas_video = null;
    // 
    this.irisC = [];

    this.init();
  }

  init() {
    this.resize();
    this.initDetection();
    this.bindEvents();
  }

  // 
  resize() {
    const tmp = [this.videoEl, this.canvasImgEl];
    for (let i = 0; i < tmp.length; i++) {
      tmp[i].width = this.options.mediaSize.width;
      tmp[i].height = this.options.mediaSize.height;
    }
    const wraperEl = document.querySelector('.wraper');
    wraperEl.style.width = `${this.options.mediaSize.width}px`;
    wraperEl.style.height = `${this.options.mediaSize.height}px`;
  }

  // 
  async initDetection() {
    // 
    // faceapi.loadTinyFaceDetectorModel('./assets/face-api/models');
    // 68
    // faceapi.loadSsdMobilenetv1Model('./assets/face-api/models');
    // faceapi.loadFaceLandmarkModel('./assets/face-api/models');
    // loadFaceRecognitionModelfaceapi.resizeResults
    // faceapi.loadFaceRecognitionModel('./assets/face-api/models');

    // facingMode 
    const mediaOpt = {
      video: { width: this.options.mediaSize.width, height: this.options.mediaSize.height, facingMode: "user" }
    };
    //  WebRTC 
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      // API
      this.mediaStreamTrack = await navigator.mediaDevices.getUserMedia(mediaOpt)
        .catch(this.mediaErrorCallback);
    } else if (navigator.webkitGetUserMedia) {
      // webkit
      this.mediaStreamTrack = await navigator.webkitGetUserMedia(mediaOpt)
        .catch(this.mediaErrorCallback);
    } else if (navigator.mozGetUserMedia) {
      // Firefox
      this.mediaStreamTrack = await navigator.mozGetUserMedia(mediaOpt)
        .catch(this.mediaErrorCallback);
    } else if (navigator.getUserMedia) {
      // API
      this.mediaStreamTrack = await navigator.getUserMedia(mediaOpt)
        .catch(this.mediaErrorCallback);
    }
    this.initVideo();
  }

  // 
  bindEvents() {
    // "" 
    // this.retryBtnEl.onclick = () => {
    //   if (this.videoEl.paused || this.videoEl.ended) {
    //     this.hideEl(this.trackBoxEl).hideEl(this.operationEl);
    //     this.videoEl.play();
    //   }
    // };
    // "" 
    // this.compareBtnEl.onclick = async () => {
    //   console.log('compareBtnEl.onclick');
    //   //  canvas 
    //   this.canvasImgEl.getContext('2d').drawImage(this.videoEl, 0, 0, this.canvasImgEl.width, this.canvasImgEl.height);
    //   const imageBase64 = this.canvasImgEl.toDataURL('image/png');
    //   // // image = image.replace('data:image/png;base64,', '');
    //   const imageEl = document.createElement('img');
    //   imageEl.src = imageBase64;
    //   const detectionWithLandmarks = await faceapi.detectSingleFace(imageEl).withFaceLandmarks();
    //   console.log(detectionWithLandmarks);
    //   return
    // };
    this.compareBtnEl.onclick = () => {
      if (this.videoEl.paused) {
        this.videoEl.play();
      }
    }
    this.pauseBtnEl.onclick = () => {
      this.pauseVideo();
    }
  }

  // 
  initVideo(stream) {
    // this.videoEl.onplay = () => {
    //   console.log('onPlay');
    //   this.onPlay();
    //   this.canvas_video = faceapi.createCanvasFromMedia(this.videoEl);
    // };
    this.videoEl.srcObject = this.mediaStreamTrack;
    // video
    // if (this.videoEl.pause) {
    //   this.compareBtnEl.style.display = 'inline-block';
    // }
  }

  pauseVideo() {
    this.videoEl.pause();
    this.timer = setTimeout(() => this.onPlay(), 10000);
    clearTimeout(this.timer)
  }

  // 
  mediaErrorCallback(error) {
    if (errorMap[error.name]) {
      alert(errorMap[error.name]);
    }
  }

  // 
  onPlay() {
    // 
    if (this.videoEl && (this.videoEl.paused || this.videoEl.ended)) {
      return;
    }
    switch(currentStep) {
      case '0':
        this.handleStep0();
        break;
      case '1':
        this.handleStep1();
        break;
      case '2':
        this.handleStep2();
        break;
      case '3':
        this.handleStep3();
        break;
      case '99':
        this.handleStep99();
        break;
    }

    this.timer = setTimeout(() => this.onPlay(), this.timeOut);
  }

  // 
  drawFaceBox(dimensions, trackBox, detections, score) {
    this.showEl(trackBox);

    //  
    trackBox.width = this.options.mediaSize.width;
    trackBox.height = this.options.mediaSize.height;

    const resizedDetections = detections.map(res => res.forSize(trackBox.width, trackBox.height));
   
    //   > matchedScore
    const faceBoxOpts = score > this.options.matchedScore ? {
      lineWidth: 2,
      textColor: 'green',
      boxColor: 'green',
      withScore: true
    } : {
      lineWidth: 2,
      textColor: 'red',
      boxColor: 'red',
      withScore: true
    };

    // 
    faceapi.drawDetection(trackBox, resizedDetections, faceBoxOpts);
  }

  showEl(el) {
    el.style.visibility = 'visible';
    return this;
  }

  hideEl(el) {
    el.style.visibility = 'hidden';
    return this;
  }

  changeTipText() {
    this.tipText.innerText = stepMap[currentStep].text;
  }
  showTip(type) {
    this.tipText.innerText = tipsMap[type];
  }

  // 
  isCloseEyes(points) {
    // :
    // 
    // 
    const x_ = points[44-1].x
    const y_ = points[44-1].y
    const w_ = points[45-1].x - points[44-1].x
    const h_ = points[48-1].y - points[44-1].y

    // video
    this.ctx_face.clearRect(0, 0, this.options.mediaSize.width, this.options.mediaSize.height);
    this.ctx_face.drawImage(this.videoEl, 0, 0, this.options.mediaSize.width, this.options.mediaSize.height);
    // -rgb(0-255)
    const frame = this.ctx_face.getImageData(0, 0, this.options.mediaSize.width, this.options.mediaSize.height);
    // 
    const p_ = Math.floor(x_+w_/2) + Math.floor(y_+h_/2) * this.options.mediaSize.width;
    // 
    const v_ = Math.floor((frame.data[p_*4+0] + frame.data[p_*4+1] + frame.data[p_*4+2])/3 );
    console.log("irisC:"+v_);

    this.irisC.push(v_);
    if(this.irisC.length>30){
      this.irisC.shift();
    }//

    // 
    let meanIrisC = this.irisC.reduce(function(sum, element){
      return sum + element;
    }, 0);
    meanIrisC = meanIrisC / this.irisC.length;
    // 
    const currentIrisC = this.irisC[this.irisC.length-1];
    if(this.irisC.length==30 && currentIrisC >= meanIrisC*this.options.closeEyesScore){
      return true
    }
    return false
  }

  isOpenMouth(points) {
    // :
    // 68-62,67-63,66-64y
    if (!points || !points.length) {
      return false
    }
    console.log(points[67].y/points[61].y,points[66].y/points[62].y,points[65].y/points[63].y);
    if (points[67].y/points[61].y > this.options.openMouthScore || points[66].y/points[62].y > this.options.openMouthScore || points[65].y/points[63].y > this.options.openMouthScore) {
      return true
    }
    return false
  }

  isShakeHead(points,detection) {
    // :
    // 31
    if (!points || !points.length) {
      return false
    }
    if (this.options.point31Pos.x > 0) {
      const absX = Math.abs(points[30].x - this.options.point31Pos.x)/detection.imageWidth;
      const absY = Math.abs(points[30].y - this.options.point31Pos.y)/detection.imageHeight;
      console.log(absX, absY);
      if (absX > this.options.point31Pos.score || absY > this.options.point31Pos.score) {
        this.options.point31Pos.count++;
      }
    }
    this.options.point31Pos.x = points[30].x;
    this.options.point31Pos.y = points[30].y;
    if (this.options.point31Pos.count > 1) {
      // 
      return true
    }
  }

  isAreaCenter(landmarks) {
    // 31x
    // 
    // console.log(landmarks.positions[30].x / this.options.mediaSize.width, landmarks.positions[30].y/this.options.mediaSize.height);
    if ((landmarks.positions[30].x/this.options.mediaSize.width) < 0.42 || (landmarks.positions[30].x/this.options.mediaSize.width) > 0.58 || (landmarks.positions[30].y/this.options.mediaSize.height) < 0.3 || (landmarks.positions[30].y/this.options.mediaSize.height) > 0.7) {
      this.showTip('center');
      return false
    }
    const faceAreaPerc = (landmarks.imageHeight * landmarks.imageWidth) / (this.options.mediaSize.width * this.options.mediaSize.height);
    // console.log(faceAreaPerc);
    if (faceAreaPerc < 0.15) {
      this.showTip('short');
      return false
    }
    if (faceAreaPerc > 0.45) {
      this.showTip('long');
      return false
    }
    this.changeTipText();
    return true
  }

  async handleStep0() {
    //  TinyFaceDetector inputSize   scoreThreshold 
    const detectionWithLandmarks = await faceapi.detectSingleFace(this.videoEl, new faceapi.TinyFaceDetectorOptions({
      inputSize: 512,
      scoreThreshold: 0.6
    })).withFaceLandmarks();

    // 
    // console.log(detectionWithLandmarks);
    if (detectionWithLandmarks) {
      console.log(detectionWithLandmarks.detection.score);
      if (detectionWithLandmarks.detection && detectionWithLandmarks.detection.score > this.options.matchedScore) {
        // console.log(` ${this.options.matchedScore}`);
        currentStep = '1';
        this.changeTipText();
        return;
      }
    }
  }

  async handleStep1() {
    // 68
    // const detectionWithLandmarks = await faceapi.detectSingleFace(this.videoEl, new faceapi.TinyFaceDetectorOptions()).withFaceLandmarks();
    // if (!detectionWithLandmarks) {
    //   return
    // }
    //
    // const resizedDetections = faceapi.resizeResults(detectionWithLandmarks, { width: this.options.mediaSize.width, height: this.options.mediaSize.height });

    const resizedDetections = await faceapi.detectSingleFace(this.videoEl).withFaceLandmarks();
    console.log(resizedDetections);
    if (resizedDetections) {
      // 68
      const landmarks = resizedDetections.landmarks;
      const positions = landmarks.positions;
      if (this.isAreaCenter(landmarks) && this.isCloseEyes(positions)) {
        console.log('');
        this.timeOut = 100;
        currentStep = '2';
        this.changeTipText();
      }
    }
  }

  async handleStep2() {
    // 68
    const detectionWithLandmarks = await faceapi.detectSingleFace(this.videoEl).withFaceLandmarks();
    console.log(detectionWithLandmarks);
    if (detectionWithLandmarks && detectionWithLandmarks.landmarks && detectionWithLandmarks.landmarks.positions) {
      const positions = detectionWithLandmarks.landmarks.positions;
      if (this.isAreaCenter(detectionWithLandmarks.landmarks) && this.isOpenMouth(positions)) {
        currentStep = '3';
        this.changeTipText();
      }
    }
  }

  async handleStep3() {
    const detectionWithLandmarks = await faceapi.detectSingleFace(this.videoEl).withFaceLandmarks();
    // console.log(detectionWithLandmarks);
    // console.log(this.options.point31Pos.count);
    if (detectionWithLandmarks && detectionWithLandmarks.detection && detectionWithLandmarks.detection.box && this.isShakeHead(detectionWithLandmarks.landmarks.positions,detectionWithLandmarks.detection)) {
      currentStep = '99';
      this.changeTipText();
    }
  }

  handleStep99() {
    this.pauseVideo();
  }

}

module.exports = FaceDetection;